/*
/* plugin enhancedLovitem 
*/

.itemContainer ~ button {
  order: 4;
}

.pretius--enhancedLovItem .debug,
.itemContainer ~ .debugAutocomplete {
  opacity: 0.5;
}

.pretius--enhancedLovItem.mask {
  max-width: 100%;
  display: flex;
  background-color: #f9f9f9;
  order: 3;
}

.pretius--enhancedLovItem.mask:hover,
.pretius--enhancedLovItem.mask.focused {
  background-color: #fff;
}

.itemContainer {
  border: 1px solid rgba(0, 0, 0, 0.125);
  min-width: 0px;
  flex-grow: 1;
  cursor: text;
  position: relative;
}

.t-Form--stretchInputs .pretius--enhancedLovItem.mask,
.t-Form-fieldContainer--stretchInputs .pretius--enhancedLovItem.mask {
  flex: 1;
}

/* disabled */
.pretius--enhancedLovItem.mask.disabled {
  opacity: 0.5;
}

.pretius--enhancedLovItem.mask.disabled .itemContainer {
  cursor: default;
}

.itemContainerBody .masking {
  background: rgba(255, 255, 255, 0);
  background: -moz-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 1) 100%);
  background: -webkit-gradient(
    left top,
    right top,
    color-stop(0%, rgba(255, 255, 255, 0)),
    color-stop(100%, rgba(255, 255, 255, 1))
  );
  background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 1) 100%);
  background: -o-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 1) 100%);
  background: -ms-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 1) 100%);
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#ffffff', GradientType=1 );
  position: absolute;
  top: 0px;
  right: 0px;
  width: 10px;
  height: 100%;
}

.itemContainer > .itemContainerBody input[type="text"].fakeCursorHandler {
  width: 100%;
  margin-top: 0.2rem;
  border: none;
  background: none;
  height: 1.8rem;
}

/*
.itemContainer > .itemContainerBody input[type=text] {
  border: none;
  background: none;
  flex-grow: 1;
  flex-shrink: 1;
  margin: 0px;
  padding: 0px;
  height: 1.8rem;
}
*/

.itemContainer > .itemContainerBody div.fakeInput {
  flex-grow: 1;
  background: silver;
  min-width: 20px;
}

/* item disabled -> tags */

.pretius--enhancedLovItem.mask.disabled div.tags > div.tag:hover {
  border-color: rgb(31, 110, 174);
  background: rgba(214, 214, 214, 0.5);
}

.pretius--enhancedLovItem.mask.disabled div.tags > div.tag > .remove {
  cursor: default;
}

/* item -> tags -> state -> expanded */
.pretius--enhancedLovItem.mask.expanded .itemContainer > .itemContainerBody > div.tags {
  flex-wrap: wrap;
  overflow: visible;
}

/* item -> tags -> */
.pretius--enhancedLovItem.mask .itemContainer > .itemContainerBody > div.tags {
  display: flex;
  flex-wrap: nowrap;
  overflow: hidden;
}

.pretius--enhancedLovItem.mask .itemContainer > .itemContainerBody > div.tags > div.tag > .value {
  flex-shrink: 1;
  flex-grow: 1;
  text-overflow: ellipsis;
  overflow: hidden;
}

.pretius--enhancedLovItem.mask .itemContainer > .itemContainerBody > div.tags > div.tag > .remove {
  flex-shrink: 0;
  flex-grow: 0;
  font-size: 1rem;
  line-height: 2rem;
  padding-left: 0.2rem;
  cursor: pointer;
  color: $base-text;
  align-self: center;
  margin-left: 0.3pc;
}

.pretius--enhancedLovItem.mask .itemContainer > .itemContainerBody > div.tags > div.tag.extra {
  background: #ffdfdf;
}

.pretius--enhancedLovItem.mask .itemContainer > .itemContainerBody > div.tags > div.tag {
  /*display: inline-block;*/
  display: flex;
  max-width: 100%;
  border: 1px solid rgba(214, 214, 214, 1);
  position: relative;
  border-radius: 6px;
  /* background: rgba(214, 214, 214, 0.5); */
  background: $shq-lov-item-tag-bg;
  flex-shrink: 0;
  color: $base-text;
  /* color: rgba(0, 0, 0, 0.5); */
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}

.pretius--enhancedLovItem.mask .itemContainer > .itemContainerBody > div.tags > div.tag:hover {
  border-color: rgb(31, 110, 174);
}

.pretius--enhancedLovItem.mask .itemContainer > .itemContainerBody > div.tags > div.tag:not(:last-child) {
  margin-right: 0.2rem;
}

.itemContainer > input[type="text"]:focus,
.itemContainer > input[type="text"]:hover {
  background-color: transparent !important;
}

.pretius--enhancedLovItem.prompt {
  border-color: rgb(31, 110, 174);
  position: absolute;
  background: #fff;
  z-index: 400;
}
.pretius--enhancedLovItem.prompt.hidden {
  display: none;
}

.pretius--enhancedLovItem.prompt > .menu .fa {
  font-size: 12px;
  line-height: 12px;
}

.pretius--enhancedLovItem.prompt > .header,
.pretius--enhancedLovItem.prompt > .menu,
.pretius--enhancedLovItem.prompt > .body {
  border-color: rgb(31, 110, 174);
  border-width: 1px;
  border-style: solid;
}

.pretius--enhancedLovItem.prompt > .header,
.pretius--enhancedLovItem.prompt > .menu {
  padding: 0.4rem;
}

.pretius--enhancedLovItem.prompt > .menu,
.pretius--enhancedLovItem.prompt > .header,
.pretius--enhancedLovItem.prompt > .body {
  border-top: none;
}

.pretius--enhancedLovItem.prompt > .header {
  position: relative;
  display: flex;
}

.pretius--enhancedLovItem.prompt > .header > .fa {
  align-self: center;
  font-size: 1.3rem;
  flex-shrink: 0;
  flex-grow: 0;
}

.pretius--enhancedLovItem.prompt > .header > input {
  border: none;
  outline: none;
  margin-right: 0.4rem;
  flex-grow: 1;
  flex-shrink: 0;
  line-height: 2rem;
  background-color: rgba(184, 205, 224, 0.09);
}

.pretius--enhancedLovItem.prompt > .body {
  overflow: auto;
}

.pretius--enhancedLovItem.prompt .fixBorder {
  display: none;
  border-top-width: 1px;
  border-top-style: solid;
  /*  border-color: rgba(0, 0, 0, 0.125);*/
  border-color: rgb(31, 110, 174);
  height: 0px;
  width: 50px;
  position: absolute;
  top: 0px;
  right: 0px;
}

.pretius--enhancedLovItem.prompt .searching,
.pretius--enhancedLovItem.prompt .nodatafound {
  text-align: center;
  padding: 6px;
}

.pretius--enhancedLovItem.prompt .searching .icon,
.pretius--enhancedLovItem.prompt .searching .text,
.pretius--enhancedLovItem.prompt .nodatafound .icon,
.pretius--enhancedLovItem.prompt .nodatafound .text {
  display: block;
}

.pretius--enhancedLovItem.prompt ul {
  list-style: none;
  margin: 0;
  position: relative;
}

.pretius--enhancedLovItem.prompt ul li.custom {
  padding: 0rem;
  cursor: pointer;
}

.pretius--enhancedLovItem.prompt ul li.default {
  padding: 0.3rem;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
}

.pretius--enhancedLovItem.prompt ul li.custom:hover,
.pretius--enhancedLovItem.prompt ul li.custom[aria-selected="true"]:hover,
.pretius--enhancedLovItem.prompt ul li.custom[aria-selected="true"][aria-highlighted="true"]:hover,
.pretius--enhancedLovItem.prompt ul li.custom[aria-selected="true"][aria-highlighted="true"],
.pretius--enhancedLovItem.prompt ul li.custom[aria-highlighted="true"],
.pretius--enhancedLovItem.prompt ul li.default:hover,
.pretius--enhancedLovItem.prompt ul li.default[aria-selected="true"]:hover,
.pretius--enhancedLovItem.prompt ul li.default[aria-selected="true"][aria-highlighted="true"]:hover,
.pretius--enhancedLovItem.prompt ul li.default[aria-selected="true"][aria-highlighted="true"],
.pretius--enhancedLovItem.prompt ul li.default[aria-highlighted="true"] {
  /* background: #1e90ff; */
  background:$shq-lov-item-hover;
  color: $blanc-shq-qc;

}

.pretius--enhancedLovItem.prompt ul li:last-child.custom:hover,
.pretius--enhancedLovItem.prompt ul li:last-child.custom[aria-selected="true"]:hover,
.pretius--enhancedLovItem.prompt ul li:last-child.custom[aria-selected="true"][aria-highlighted="true"]:hover,
.pretius--enhancedLovItem.prompt ul li:last-child.custom[aria-selected="true"][aria-highlighted="true"],
.pretius--enhancedLovItem.prompt ul li:last-child.custom[aria-highlighted="true"],
.pretius--enhancedLovItem.prompt ul li:last-child.default:hover,
.pretius--enhancedLovItem.prompt ul li:last-child.default[aria-selected="true"]:hover,
.pretius--enhancedLovItem.prompt ul li:last-child.default[aria-selected="true"][aria-highlighted="true"]:hover,
.pretius--enhancedLovItem.prompt ul li:last-child.default[aria-selected="true"][aria-highlighted="true"],
.pretius--enhancedLovItem.prompt ul li:last-child.default[aria-highlighted="true"] {
  -webkit-box-shadow: inset 0px -19px 15px -15px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: inset 0px -19px 15px -15px rgba(0, 0, 0, 0.1);
  box-shadow: inset 0px -19px 15px -15px rgba(0, 0, 0, 0.1);
}

.pretius--enhancedLovItem.prompt ul li.custom[aria-selected="true"],
.pretius--enhancedLovItem.prompt ul li.default[aria-selected="true"] {
  /*text-shadow: 0 0 0.001px rgba(0,0,0, 0.5);*/
  /*border-right: 1px solid #ecf3ff;*/
  background:$shq-lov-item-tag-bg ; /*  #f2f2f2; */
}

/*
.pretius--enhancedLovItem.prompt .menu {
}
*/
.pretius--enhancedLovItem.prompt .menu .view .collapsed {
  display: none;
}

.pretius--enhancedLovItem.prompt .menu .view .expanded {
  display: block;
}

.pretius--enhancedLovItem.prompt .menu .view.expanded .collapsed {
  display: block;
}

.pretius--enhancedLovItem.prompt .menu .view.expanded .expanded {
  display: none;
}

/* default sort state */
.pretius--enhancedLovItem.prompt .menu .sort .desc {
  display: block;
}

.pretius--enhancedLovItem.prompt .menu .sort .asc {
  display: none;
}

/* sorted state */
.pretius--enhancedLovItem.prompt .menu .sort.desc .desc,
.pretius--enhancedLovItem.prompt .menu .sort.asc .asc {
  display: none;
}

.pretius--enhancedLovItem.prompt .menu .sort.desc .asc,
.pretius--enhancedLovItem.prompt .menu .sort.asc .desc {
  display: block;
}

/*popup*/
.ui-dialog.pretius--enhancedLovItem .body {
  overflow: auto;
  position: relative;
}

.ui-dialog.pretius--enhancedLovItem .body .nodatafoundContainer,
.ui-dialog.pretius--enhancedLovItem .body .searchingForResults {
  padding: 20px 0;
}

.ui-dialog.pretius--enhancedLovItem .body.searching,
.ui-dialog.pretius--enhancedLovItem .body.nodatafound {
  display: flex;
  justify-content: center;
  padding: 0px;
}

.ui-dialog.pretius--enhancedLovItem .body .searchingForResults,
.ui-dialog.pretius--enhancedLovItem .body .nodatafoundContainer {
  align-self: center;
  text-align: center;
}

.ui-dialog.pretius--enhancedLovItem .body .searchingForResults .icon > .fa,
.ui-dialog.pretius--enhancedLovItem .body .nodatafoundContainer .icon > .fa {
  font-size: 50px;
}

.ui-dialog.pretius--enhancedLovItem .body .searchingForResults .text,
.ui-dialog.pretius--enhancedLovItem .body .nodatafoundContainer .text {
  padding-top: 10px;
  font-size: 14px;
  line-height: 20px;
}

.ui-dialog.pretius--enhancedLovItem.selectViaRows td {
  cursor: pointer;
}

.ui-dialog.pretius--enhancedLovItem .footer {
  border-top: 1px solid rgba(0, 0, 0, 0.05);
  display: flex;
}

.ui-dialog.pretius--enhancedLovItem .footer label {
  margin-left: 10px;
  cursor: pointer;
}

.ui-dialog.pretius--enhancedLovItem .footer label {
  line-height: 32px;
  font-size: 12px;
}

.ui-dialog.pretius--enhancedLovItem .footer .fakeCheckbox {
  line-height: 32px;
}

.ui-dialog.pretius--enhancedLovItem .footer .footer-buttons {
  text-align: right;
}

.ui-dialog.pretius--enhancedLovItem .header.paginationOnly .paginationContainer {
  text-align: right;
}

.ui-dialog.pretius--enhancedLovItem .header .paginationContainer .fa,
.ui-dialog.pretius--enhancedLovItem .header .paginationContainer {
  text-align: right;
  font-size: 12px;
  line-height: 30px;
}

.ui-dialog.pretius--enhancedLovItem .header .paginationContainer .prev {
  margin-right: 5px;
}

.ui-dialog.pretius--enhancedLovItem .header .paginationContainer .next {
  margin-left: 5px;
}

.ui-dialog.pretius--enhancedLovItem .footer .footer-showSelected.inactive {
  opacity: 0.5;
}

.ui-dialog.pretius--enhancedLovItem .footer .footer-showSelected,
.ui-dialog.pretius--enhancedLovItem .footer .footer-buttons {
  flex-shrink: 1;
  flex-grow: 1;
}

.ui-dialog.pretius--enhancedLovItem .header {
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
  display: flex;
}

.ui-dialog.pretius--enhancedLovItem .header > div {
  flex-shrink: 1;
  flex-grow: 1;
}

.ui-dialog.pretius--enhancedLovItem .searchContainer {
  position: relative;
  text-align: left;
}

.ui-dialog.pretius--enhancedLovItem .searchContainer input {
  padding: 5px;
  line-height: 16px;
  width: 100%;
}

.ui-dialog.pretius--enhancedLovItem .rowsPerPageContainer select {
  margin-left: 8px;
  padding: 5px;
  line-height: 16px;
  height: 3rem;
}

.ui-dialog.pretius--enhancedLovItem .searchContainer.multiple input {
  width: 100%;
}

.ui-dialog.pretius--enhancedLovItem .searchContainer .fa {
  top: 0px;
  line-height: 34px;
  position: absolute;
  right: 15px;
  opacity: 0.5;
}

.ui-dialog.pretius--enhancedLovItem input ~ .search,
.ui-dialog.pretius--enhancedLovItem input.empty ~ .clear {
  display: none;
}

.ui-dialog.pretius--enhancedLovItem input.empty ~ .search,
.ui-dialog.pretius--enhancedLovItem input ~ .clear {
  display: block;
}

.ui-dialog.pretius--enhancedLovItem input ~ div {
  top: 0px;
  line-height: 34px;
  position: absolute;
  right: 0px;
}

.ui-dialog.pretius--enhancedLovItem input ~ div > .search,
.ui-dialog.pretius--enhancedLovItem input.empty ~ div > .clear {
  display: none;
}

.ui-dialog.pretius--enhancedLovItem input.empty ~ div > .search,
.ui-dialog.pretius--enhancedLovItem input ~ div > .clear {
  opacity: 0.5;
  display: block;
}

.ui-dialog.pretius--enhancedLovItem .clear {
  cursor: pointer;
}

.ui-dialog.pretius--enhancedLovItem .clear:hover {
  opacity: 1;
}

.ui-dialog.pretius--enhancedLovItem .header,
.ui-dialog.pretius--enhancedLovItem .footer {
  padding: 10px 15px 10px 42px;
}

.ui-dialog.pretius--enhancedLovItem .header.hidden,
.ui-dialog.pretius--enhancedLovItem .footer.hidden {
  display: none;
}

/* .ui-dialog.pretius--enhancedLovItem .body table {
}
*/

.ui-dialog.pretius--enhancedLovItem .u-Report-sort {
  white-space: nowrap;
}

.ui-dialog.pretius--enhancedLovItem th:first-child,
.ui-dialog.pretius--enhancedLovItem table.stickyHeaders th:first-child,
.ui-dialog.pretius--enhancedLovItem td.t-Report-cell:first-child {
  width: 42px;
  min-width: 42px;
  max-width: 42px;
}

.ui-dialog.pretius--enhancedLovItem th {
  border-top: none;
}

.ui-dialog.pretius--enhancedLovItem table.stickyHeaders {
  position: absolute;
  top: 0px;
  width: 100%;
}

.ui-dialog.pretius--enhancedLovItem table.stickyHeaders th {
  background: $shq-lov-item-header;
  position: relative;
}

.ui-dialog.pretius--enhancedLovItem tr.hiddenDueToFilter {
  display: none;
}

.highlight {
  background: yellow;
  color: $base-text;
  font-weight: 700;
}

/* Bouton reset refresh */
.clearFiltering {
  & .fam-is-warning::after {
    background-color: $header-IRR-color;
  }
}
/* information region */
.ui-dialog.pretius--enhancedLovItem .information.hidden {
  display: none;
}

.ui-dialog.pretius--enhancedLovItem .information {
  background: #f3f3f3;
  position: relative;
  min-height: 30px;
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.ui-dialog.pretius--enhancedLovItem .information .text {
  padding-top: 5px;
  padding-bottom: 5px;
  line-height: 20px;
  font-size: 12px;
}

/* column filtering and sorting */

.ui-dialog.pretius--enhancedLovItem table.stickyHeaders th.is-active .a-Icon,
.ui-dialog.pretius--enhancedLovItem table.stickyHeaders th.is-active .fa {
  color: #fff;
}

.ui-dialog.pretius--enhancedLovItem table.stickyHeaders th.is-active,
.ui-dialog.pretius--enhancedLovItem .columnOperations {
  background-color: #344e7a;
  border-color: rgb(31, 110, 174);
}

.ui-dialog.pretius--enhancedLovItem table.stickyHeaders th.is-active a {
  color: #fff;
}

.ui-dialog.pretius--enhancedLovItem .columnOperations {
  position: absolute;
  top: 0px;
}

.ui-dialog.pretius--enhancedLovItem .columnOperations .a-Icon {
  color: #fff;
}

.ui-dialog.pretius--enhancedLovItem .sorting,
.ui-dialog.pretius--enhancedLovItem .filtering {
  position: relative;
}

.ui-dialog.pretius--enhancedLovItem .filtering .fa {
  position: absolute;
  right: 7px;
  top: 7px;
  color: #fff;
  opacity: 0.5;
}

.ui-dialog.pretius--enhancedLovItem .sorting a {
  display: inline-block;
  width: auto;
  padding: 5px 10px;
}

.ui-dialog.pretius--enhancedLovItem .sorting {
  border-left: 0px none $gouvernement-qc;
  border-right: 0px none $gouvernement-qc;
  border-top: 0px none $gouvernement-qc;
}

.ui-dialog.pretius--enhancedLovItem .filtering {
  border: 1px solid $gouvernement-qc;
  background: #405a88;
}

.ui-dialog.pretius--enhancedLovItem .filtering input {
  border: none;
  background: transparent;
  padding: 5px;
  width: 100%;
  color: #fff;
}

.ui-dialog.pretius--enhancedLovItem .fakeRadio,
.ui-dialog.pretius--enhancedLovItem .fakeCheckbox {
  cursor: pointer;
}

.ui-dialog.pretius--enhancedLovItem input[type="radio"]:not(:checked) ~ .fa-circle-o,
.ui-dialog.pretius--enhancedLovItem input[type="radio"]:checked ~ .fa-dot-circle-o {
  display: inline-block;
}

.ui-dialog.pretius--enhancedLovItem input[type="radio"],
.ui-dialog.pretius--enhancedLovItem input[type="radio"]:checked ~ .fa-circle-o,
.ui-dialog.pretius--enhancedLovItem input[type="radio"]:not(:checked) ~ .fa-dot-circle-o {
  display: none;
}

.ui-dialog.pretius--enhancedLovItem input[type="checkbox"]:not(:checked) ~ .fa-square-o,
.ui-dialog.pretius--enhancedLovItem input[type="checkbox"]:checked ~ .fa-check-square-o {
  display: inline-block;
}

/* hack to make label and checkbox work, but not to show checkbox itself*/
.ui-dialog.pretius--enhancedLovItem .footer input[type="checkbox"] {
  position: fixed;
  opacity: 0;
  top: -10px;
  left: -10px;
}

.ui-dialog.pretius--enhancedLovItem th input[type="checkbox"],
.ui-dialog.pretius--enhancedLovItem td input[type="checkbox"],
.ui-dialog.pretius--enhancedLovItem input[type="checkbox"]:checked ~ .fa-square-o,
.ui-dialog.pretius--enhancedLovItem input[type="checkbox"]:not(:checked) ~ .fa-check-square-o {
  display: none;
}

.ui-dialog.pretius--enhancedLovItem tr:nth-child(even).extraValue td.t-Report-cell,
.ui-dialog.pretius--enhancedLovItem tr:nth-child(even).extraValue.selected td.t-Report-cell,
.ui-dialog.pretius--enhancedLovItem tr:nth-child(odd).extraValue td.t-Report-cell,
.ui-dialog.pretius--enhancedLovItem tr:nth-child(odd).extraValue.selected td.t-Report-cell {
  background: #ffdfdf;
}

.ui-dialog.pretius--enhancedLovItem tr td.t-Report-cell {
  white-space: nowrap;
}

.ui-dialog.pretius--enhancedLovItem tr:nth-child(odd):not(.selected) td.t-Report-cell {
  background: $shq-lov-item-odd !important;
}
.ui-dialog.pretius--enhancedLovItem
  tr:not(.selected)
  td.t-Report-cell:hover
  .ui-dialog.pretius--enhancedLovItem
  tr:nth-child(odd).selected
  td.t-Report-cell,
.ui-dialog.pretius--enhancedLovItem tr.selected td.t-Report-cell {
  /*background: rgba(255, 249, 0, 0.05); */
  background: $shq-lov-item-selected;
  color: $blanc-shq-qc;
  text-shadow: 0 0 0.001px rgba(0, 0, 0, 0.5);
}
.ui-dialog.pretius--enhancedLovItem tr:not(.selected) td.t-Report-cell:hover,
.ui-dialog.pretius--enhancedLovItem tr.selected:hover .t-Report-cell,
.ui-dialog.pretius--enhancedLovItem tr.selected:nth-child(odd):hover .t-Report-cell {
  /* background: rgba(255, 249, 0, 0.1) !important; */
  color: $blanc-shq-qc;
  background: $shq-lov-item-selected-hover !important;
}

.ui-dialog.pretius--enhancedLovItem tr.selected td.t-Report-cell .fakeCheckbox {
  text-shadow: none;
}

/* sizes */

/* default */
.itemContainerBody {
  height: 100%;
}

.itemContainer > .itemContainerBody > div.tags > div.tag {
  padding: 0 0.5rem 0 0.5rem;
  margin-top: 0.2rem;
  height: inherit;
  line-height: inherit;
  font-size: inherit;
}

.itemContainer > .itemContainerBody > div.tags > div.tag.summary {
  cursor: pointer;
}

.itemContainer {
  padding: 0 0.2rem 0.2rem 0.2rem;
  overflow: hidden;
}

.itemContainer .placeholder {
  line-height: 2.2rem;
  font-size: 1.2rem;
  opacity: 0.5;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

/* large */
.t-Form-fieldContainer--large .itemContainer > .itemContainerBody > div.tags > div.tag {
  padding: 0 0.6rem 0 0.6rem;
  margin-top: 0.3rem;
  height: 2.4rem;
  line-height: inherit;
  font-size: inherit;
}

.t-Form-fieldContainer--large .itemContainer > .itemContainerBody > div.tags > div.tag > .remove {
  line-height: 2.8rem;
  font-size: 1rem;
}

.t-Form-fieldContainer--large .itemContainer {
